import React from 'react';
import { Button, Form, Input, notification } from 'antd';
import './LinkManager.scss';
import { useState } from 'react';
import { ethers } from 'ethers';
import { LinkPrefixSelect } from '../LinkPrefixSelect';
import { LinkPrefixType } from '../../models/wnft';
import { useEffect } from 'react';

export interface LinkManagerProps {
    tokenId: number;
    contract: ethers.Contract;
}

export function LinkManager({ tokenId, contract }: LinkManagerProps) {

    const [linkPrefix, setLinkPrefix] = useState<LinkPrefixType>('https://');
    const [currentLink, setCurrentLink] = useState<string>();
    const [loading, setLoading] = useState<boolean>();

    const updateCurrentLink = async () => {
        try {
            const linkResp = await contract.getTokenUrl(tokenId);
            setCurrentLink(linkResp.toString());
        } catch (e) {
            notification.error({
                message: 'Get Current Link Error',
                description: JSON.stringify(e)
            })
        }
    }

    const onFinish = async (value: any) => {
        setLoading(true);
        
        const { link } = value;

        try {
            const setLinkResp = await contract.setTokenUrl(+tokenId, `${linkPrefix}${link}`);
            await setLinkResp.wait();
            notification.success({
                message: 'Set Link Success'
            })
            updateCurrentLink();
            setLoading(false);
        } catch (e) {
            notification.error({
                message: 'Set Link Failed',
                description: JSON.stringify(e)
            });
            setLoading(false);
        }
    }

    useEffect(() => {
        updateCurrentLink();
    }, []);

    return (
        <div className='link-manager'>
            <p>The WNFT is currently pointing to {currentLink}</p>
            <Form
                name="basic"
                labelCol={{ span: 8 }}
                wrapperCol={{ span: 16 }}
                onFinish={onFinish}
                autoComplete="off"
            >
                <Form.Item
                    label="link"
                    name="link"
                    rules={[{ required: true, message: 'Please input link' }]}
                >
                    <Input addonBefore={<LinkPrefixSelect onChange={(prefix: LinkPrefixType) => setLinkPrefix(prefix)} />} />
                </Form.Item>

                <Form.Item wrapperCol={{ offset: 8, span: 16 }}>
                    <Button type="primary" htmlType="submit" loading={loading}>
                        Submit
                    </Button>
                </Form.Item>
            </Form>

        </div>
    );
};
